import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { HeaderModule } from 'src/app/components/header/header.module';
import { AngularFirestoreDocument, AngularFirestore } from '@angular/fire/firestore';
import { AuthService } from 'src/app/services/auth.service';
import { Observable } from 'rxjs';
export interface Item { name: string; }

@Component({
  selector: 'app-upload-keys',
  templateUrl: './upload-keys.page.html',
  styleUrls: ['./upload-keys.page.scss'],
})
export class UploadKeysPage implements OnInit {
  selectedFile
  coffeeOrders;
  uploadedKeys: boolean = false
  private itemsCollection: AngularFirestoreDocument<Item>;
  @ViewChild('jsonFile') input: ElementRef;

  constructor(private auth: AuthService, private afs: AngularFirestore) {
    this.itemsCollection = afs.doc("service-accounts/" + String(localStorage.getItem('user_uid')));
    // this.items = this.itemsCollection.valueChanges();
  }

  ngOnInit() {
    this.auth.isUploadedKeys().subscribe(res => {
      console.log(res.payload.data())
      if (res.payload.data()) {
        this.uploadedKeys = true
      }
    })
  }

  ngAfterViewInit() {
    console.log("ngAfterViewInit")

  }

  items: Observable<Item[]>;


  async getJSONFromFile() {
    let fileReader = new FileReader();
    const promise = new Promise(function (resolve, reject) {
      fileReader.onload = (e) => {
        resolve(JSON.parse(String(fileReader.result)));
      }
    });
    fileReader.readAsText(this.input.nativeElement.files[0]);
    return promise


  }
  onSubmit() {


    this.getJSONFromFile().then(data => {
      console.log(data)
      this.auth.updateUserData(data)
    })



  }
}
